section > .liquid-container .liquid-child {
  height: 100vh;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  transform-style: preserve-3d;
  padding-top: 120px;
  box-sizing: border-box;
  visibility: hidden;

  @media #{$mobile-only} {
    height: calc(100vh - 25vw);
    padding-top: 110px;
  }
}
